.grid12 {
  display: grid;
  grid-template-columns: [center-start] repeat(12, [col-start] minmax(0px, 73px) [col-end]) [center-end];
  column-gap: 24px;
  align-items: center;
}

.grid12-5 {
  grid-column: span 5;
}

.grid12-6 {
  grid-column: span 6;
}

.grid12-7 {
  grid-column: span 7;
}

.flex {
  display: flex;
}
.flex-col {
  flex-direction: column;
}

.flexible {
  flex-grow: 1;
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  80% {
    transform: translateX(1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }
  80% {
    transform: translateX(-1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 16px;
  box-sizing: border-box;
}
@media only screen and (max-width: 600px) {
  html {
    font-size: 14px;
  }
}

body {
  box-sizing: border-box;
}

.container {
  max-width: 1140px;
  margin: auto;
}

img {
  width: 100%;
}

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.5;
  color: #000;
}

p,
a {
