.header {
  height: 70px;
}

.btn {
  display: inline-flex;
  position: relative;
  align-items: center;
  justify-content: center;
  height: 2.5em;
  padding: 0 1em;
  transition: background-color 0.5s, box-shadow 0.5s;
  border: 0;
  border-radius: 8px;
  outline: 0;
  text-decoration: none;
  color: inherit;
  border: solid 0.25px black;
  box-shadow: 0 3px 10px rgba(27, 26, 27, 0.2);
  font-size: 1.25em;
  font-weight: bold;
  cursor: pointer;
}
.btn:hover {
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}
.btn:active, .btn:focus {
  outline: none;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
.btn--fill {
  background-color: #009fe3;
  color: #fff;
  border: solid 2px #009fe3;
}
.btn--empty {
  background-color: none;
  color: #009fe3;
  border: solid 2px #009fe3;
}
.btn--underline {
  background-color: none;
  color: #000;
  border: none;
  box-shadow: none;
  text-decoration: underline;
  text-underline-offset: 8px;
}

.buttons--wrapper {
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
  min-width: 324px;
  max-width: fit-content;
  margin: auto;
  column-gap: 24px;
  row-gap: 1em;
  padding-bottom: 1em;
}
.buttons--wrapper .link {
  grid-row: 2;
}
.buttons--wrapper .btn,
.buttons--wrapper .link {
  font-size: 1.125em;
  height: 2.25em;
}

.link {
  display: inline-flex;
  position: relative;
  align-items: center;
  justify-content: center;
  height: 2.5em;
  padding: 0 1em;
  transition: background-color 0.5s, box-shadow 0.5s;
  border: 0;
  border-radius: 8px;
  outline: 0;
  text-decoration: none;
  color: inherit;
  border: solid 0.25px black;
  box-shadow: 0 3px 10px rgba(27, 26, 27, 0.2);
  font-size: 1.25em;
  font-weight: bold;
  cursor: pointer;
}
.link--underline {
  background-color: none;
  color: #000;
  border: none;
  box-shadow: none;
  text-decoration: underline;
  text-underline-offset: 8px;
}

.border {
  border-style: solid;
  border-width: 2px;
}
.border.b--red {
  border-color: red;
}
.border.b--yellow {
  border-color: yellow;
}
.border.b--blue {
  border-color: blueviolet;
}

.module.textbox .textbox__text {
  grid-column: col-start 2/col-end 11;
}

.textbox__buttons {
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
  max-width: fit-content;
  margin: auto;
  column-gap: 24px;
  row-gap: 1em;
  padding-bottom: 1em;
}
.textbox__buttons .link {
  grid-row: 2;
}

.module.textbox.bg-blue, .module.textbox.bg-blue-gradient,
.textbox .container.bg-blue,
.textbox .container.bg-blue-gradient {
  color: #fff;
}
.module.textbox.bg-blue .btn--fill, .module.textbox.bg-blue-gradient .btn--fill,
.textbox .container.bg-blue .btn--fill,
.textbox .container.bg-blue-gradient .btn--fill {
  background-color: #fff;
  color: #009fe3;
  border: solid 2px #fff;
}
.module.textbox.bg-blue .btn--empty, .module.textbox.bg-blue-gradient .btn--empty,
.textbox .container.bg-blue .btn--empty,
.textbox .container.bg-blue-gradient .btn--empty {
  color: #fff;
  border: solid 2px #fff;
}
.module.textbox.bg-blue .link--underline, .module.textbox.bg-blue-gradient .link--underline,
.textbox .container.bg-blue .link--underline,
.textbox .container.bg-blue-gradient .link--underline {
  color: #fff;
}
.module.textbox.bg-blue,
.textbox .container.bg-blue {
  background-color: #009fe3;
}
.module.textbox.bg-blue-gradient,
.textbox .container.bg-blue-gradient {
  background: linear-gradient(180deg, #009fe3 0%, #083f74 100%);
}

.height--90vh {
  height: calc(90vh - 70px);
}
.height--75vh {
  height: calc(75vh - 70px);
}
.height--50vh {
  height: calc(50vh - 70px);
}

.teaser {
  position: relative;
}
.teaser__content, .teaser__text {
  row-gap: 3.5rem;
}
.teaser__bg-image {
  opacity: 1;
  background-size: cover;
  background-position: center center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -2;
}
.teaser__bg-gradient {
  background-size: cover;
  background-position: center center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
.teaser.tc--blue-light {
  color: #009fe3;
}
.teaser.tc--white {
  color: #fff;
}
.teaser.tc--white .btn--fill {
  background-color: #fff;
  color: #009fe3;
  border: solid 2px #fff;
}
.teaser.tc--white .btn--empty {
  color: #fff;
  border: solid 2px #fff;
}
.teaser.tc--white .link--underline {
  color: #fff;
}
@media only screen and (max-width: 1250px) {
  .teaser .container {
    padding-left: 7%;
    padding-right: 7%;
  }
}
@media only screen and (max-width: 600px) {
  .teaser .buttons--wrapper {
    grid-auto-flow: row;
    min-width: 60%;
  }
}

